// components/Projects.js
import React from 'react';
import './Projects.css';

const projects = [
  {
    title: 'Portfolio Website',
    description: 'A personal portfolio website showcasing my projects and skills.',
    techStack: ['React', 'CSS', 'JavaScript'],
    link: 'https://yourportfolio.com'
  },
  {
    title: 'E-commerce App',
    description: 'A full-featured e-commerce platform with payment integration and user authentication.',
    techStack: ['Node.js', 'Express', 'MongoDB', 'React'],
    link: 'https://myecommerceapp.com'
  },
  {
    title: 'API Development Project',
    description: 'Developed a RESTful API for managing a database of user data with authentication.',
    techStack: ['Node.js', 'Express', 'PostgreSQL'],
    link: 'https://myapi.com'
  }
];

const Projects = () => {
  return (
    <section id="projects" className="projects-section">
      <h2>Projects</h2>
      <div className="projects-grid">
        {projects.map((project, index) => (
          <div className="project-card" key={index}>
            <h3>{project.title}</h3>
            <p>{project.description}</p>
            <div className="tech-stack">
              {project.techStack.map((tech, index) => (
                <span key={index} className="tech-item">{tech}</span>
              ))}
            </div>
            <a href={project.link} target="_blank" rel="noopener noreferrer" className="btn">
              View Project
            </a>
          </div>
        ))}
      </div>
    </section>
  );
};

export default Projects;
