import React from 'react';
import { Canvas } from '@react-three/fiber';
import { OrbitControls, Text } from '@react-three/drei';
import * as THREE from 'three';

const skills = [
  'JavaScript', 'Reactjs', 'Hibernate', 'Spring Boot', 'Bootstarp', 
  'Java', 'Oracle', 'MongoDB', 'Git', 'GitHub'
];

const SkillGlobe = () => {
  return (
    <Canvas style={{ height: '400px', background: '#f4f4f9' }}>
      <ambientLight intensity={0.5} />
      <pointLight position={[10, 10, 10]} />
      <SkillSphere />
      <OrbitControls enableZoom={false} />
    </Canvas>
  );
};

const SkillSphere = () => {
  const radius = 4;
  const skillTags = skills.map((skill, index) => {
    const phi = Math.acos(-1 + (2 * (index + 1)) / skills.length);
    const theta = Math.sqrt(skills.length * Math.PI) * phi;

    return (
      <Text
        key={index}
        position={[
          radius * Math.cos(theta) * Math.sin(phi),
          radius * Math.sin(theta) * Math.sin(phi),
          radius * Math.cos(phi),
        ]}
        fontSize={0.5}
        color="#007bff"
        anchorX="center"
        anchorY="middle"
      >
        {skill}
      </Text>
    );
  });

  return <group>{skillTags}</group>;
};

export default SkillGlobe;